//HERO FOR ABOUT-SERVICES-CONTACT

.hero-container{
    overflow: hidden;

    .hero-wrapper{
        width: 100%;
        height: 100vh;
        background: linear-gradient(
            rgba(0,0,0,0.3) 0%,
            rgba(0,0,0,0.3) 100%);

        img{
            width: 100%;
            height: 100vh;
            position: relative;
            object-position: center;
            object-fit: cover;
            z-index: -1;  
        }
    }
}

.hero-title{
    position: absolute;
    top: 75px;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;

    h2{
        color: $primary-white;
        letter-spacing: 8px;
        text-align: center;
        line-height: 1.4;
    }

}


// BANNER IMAGES

.banner-container{
    width: 100%;
    img{
        width: 100%;
        height: auto;
        overflow: hidden;
    }
}


//HEADERLINE

.header-line{
    background-color: $primary-red;
    height: 5px;
    width: 50px;
    margin: 16px 0 5.5% 0;
    border: none;
}


// WIDGET TITLES

.widget-title{
    margin-top: 7%;
}


// PAGE

.page-content{
    width: 75%;
    margin: 4% auto 0 auto;

    h2{
        text-transform: capitalize;
        letter-spacing: 2px;
        text-align: center;
        margin-bottom: 5%;
    }

    p{
        margin: 0 auto 3% auto;
        line-height: 2;
        max-width: 750px;
        letter-spacing: 1px;
    }
}